.vetskitchen-checklist-slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  color: #f7f8fa;
  background-color: #008077;
}
.vetskitchen-checklist-slider .slick-track {
  display: flex;
  align-items: center;
}
.vetskitchen-checklist-slider__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px 11px 15px;
  font-size: 30px;
  line-height: 1;
  font-weight: 600;
}
.vetskitchen-checklist-slider__item .icon {
  position: relative;
  top: -5px;
  margin-right: 10px;
  font-size: 42px;
}
.vetskitchen-checklist-slider__item.slick-slide {
  display: flex;
  justify-content: center;
}
@media screen and (max-width: 767px) {
  .vetskitchen-checklist-slider__item {
    font-size: 25px;
  }
  .vetskitchen-checklist-slider__item .icon {
    font-size: 35px;
  }
}